@import url("https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&family=Teko:wght@300;400;500;600;700&family=Orbitron:wght@400;500;600;700;800;900&display=swap");

:root {
  /* Polices principales - style technique/futuriste comme dans le poster */
  --font-body: "Rajdhani", sans-serif;
  --font-heading: "Teko", sans-serif;
  --font-tech: "Orbitron", sans-serif;

  --font-weight-body: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --font-weight-heading: 600;

  --color-black: black;
  --color-black-alpha: rgba(0, 0, 0, 0.7);
  --color-white: white;
  --color-grey: #767474;
  --color-grey-dark: #3e3e3e;
  --color-Avatar: #fcc088;
  --color-Avatar-light: #fcc088;
  --color-red: #d42027; /* Rouge du poster */
}

.OumaPortfolio-helper {
  display: none;
}

html,
body,
.app {
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-weight: var(--font-weight-body);
}

body {
  font-size: clamp(18px, 3.5vmin, 26px);
  line-height: 110%;

  // Responsive font sizes
  @media (max-width: 768px) {
    font-size: clamp(16px, 4vw, 20px);
  }

  @media (max-width: 480px) {
    font-size: clamp(14px, 4vw, 18px);
  }
}
.canvas {
  background: transparent !important;
}
.app {
  background-color: var(--color-black);
  min-height: 100vh;
  width: 100%;
  color: #f9f9f9;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  h8 {
    font-family: var(--font-body);
    font-weight: 300;
    margin: 5px;
  }

h1 {
   font-size: clamp(18px, 5vmin, 70px);


    @media (max-width: 1024px) {
      font-size: clamp(28px, 8vmin, 80px);
    }

    @media (max-width: 768px) {
      font-size: clamp(24px, 7vmin, 60px);
    }

    @media (max-width: 480px) {
      font-size: clamp(20px, 6vmin, 45px);
    }
  }

  h2 {
    font-size: clamp(30px, 11vmin, 100px);

    @media (max-width: 1024px) {
      font-size: clamp(26px, 8vmin, 70px);
    }

    @media (max-width: 768px) {
      font-size: clamp(22px, 6vmin, 50px);
    }

    @media (max-width: 480px) {
      font-size: clamp(18px, 5vmin, 35px);
    }
  }

  h3 {
    font-size: clamp(24px, 6.5vmin, 60px);

    @media (max-width: 1024px) {
      font-size: clamp(22px, 5.5vmin, 45px);
    }

    @media (max-width: 768px) {
      font-size: clamp(20px, 5vmin, 35px);
    }

    @media (max-width: 480px) {
      font-size: clamp(16px, 4vmin, 28px);
    }
  }

  h4 {
    font-size: clamp(20px, 4vmin, 40px);

    @media (max-width: 1024px) {
      font-size: clamp(18px, 3.5vmin, 30px);
    }

    @media (max-width: 768px) {
      font-size: clamp(16px, 3vmin, 24px);
    }

    @media (max-width: 480px) {
      font-size: clamp(14px, 2.5vmin, 20px);
    }
  }

  h8 {
      font-size: clamp(10px, 2vmin, 17px);

    @media (max-width: 1024px) {
      font-size: clamp(16px, 3vmin, 24px);
    }

    @media (max-width: 768px) {
      font-size: clamp(10px, 2vmin, 17px);
    }

    @media (max-width: 480px) {
      font-size:0px;
    }
  }

  a,
  a:visited {
    color: var(--color-Avatar-light);
    pointer-events: all;

    &:hover {
      color: var(--color-Avatar);
    }
  }
}

.top-bar {
  position: absolute;
  top: 1em;
  left: 1em;
  right: 1em;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 80%;
  pointer-events: none;

  @media (max-width: 768px) {
    top: 0.5em;
    left: 0.5em;
    right: 0.5em;
    font-size: 70%;
  }

  @media (max-width: 480px) {
    font-size: 60%;
  }

  .left,
  .right {
    display: flex;
    align-items: center;
    gap: 0.5em;

    @media (max-width: 480px) {
      gap: 0.3em;
    }
  }

  .left {
    gap: 1em;

    @media (max-width: 768px) {
      gap: 0.7em;
    }

    @media (max-width: 480px) {
      gap: 0.5em;
    }
  }

  .left > * {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    display: none;
  }

  button {
    --size: 40px;
    --border-color: var(--color-grey);

    display: none;
    width: var(--size);
    height: var(--size);
    background-color: var(--color-black-alpha);
    border: solid 2px var(--border-color);
    border-radius: 40px;
    cursor: pointer;
    position: relative;

    align-items: center;
    justify-content: center;

    pointer-events: all;

    @media (max-width: 768px) {
      --size: 35px;
      border-width: 1.5px;
    }

    @media (max-width: 480px) {
      --size: 30px;
      border-width: 1px;
    }

    svg {
      transition: transform 0.2s ease-in-out;

      @media (max-width: 480px) {
        transform: scale(0.8);
      }
    }

    &.show-unless {
      display: flex;
    }

    &[data-off] {
      svg {
        transform: scale(0.8);

        @media (max-width: 480px) {
          transform: scale(0.6);
        }
      }

      &::after {
        content: "";
        width: 100%;
        height: 2px;
        position: absolute;
        top: 50%;
        left: 50%;
        background-color: var(--border-color);
        transform: translate(-50%, -50%) rotate(-45deg);

        @media (max-width: 480px) {
          height: 1.5px;
        }
      }
    }

    &:hover,
    &:active {
      --border-color: var(--color-Avatar);
    }
  }
}

.count {
  font-variant-numeric: tabular-nums;
}

.health-bar {
  width: 260px;
  height: 20px;
  border: 2px solid var(--color-grey);
  background-color: var(--color-black-alpha);

  overflow: hidden;
  position: relative;

  @media (max-width: 768px) {
    width: 200px;
    height: 16px;
  }

  @media (max-width: 480px) {
    width: 150px;
    height: 12px;
  }

  &::after {
    content: "";
    position: absolute;
    inset: 5px;
    background-color: var(--color-Avatar);
    transform-origin: left center;
    transform: scaleX(
      calc(8000000000000000000000000000000000000 * var(--health))
    );

    @media (max-width: 480px) {
      inset: 3px;
    }
  }
}

