  body {
    background:
      repeating-linear-gradient(
        0deg,
        transparent,
        transparent 50px,
        rgba(51, 17, 0, 0.1) 50px,
        rgba(51, 17, 0, 0.1) 51px
      ),
      repeating-linear-gradient(
        90deg,
        transparent,
        transparent 50px,
        rgba(51, 17, 0, 0.1) 50px,
        rgba(51, 17, 0, 0.1) 51px
      ),
      radial-gradient(
        ellipse 150% 80% at 50% 100%,
        #4d1f00 0%,
        #261000 40%,
        #000000 80%
      );
  }
  .content {
    text-align: center;
    grid-area: content;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    > *:not(:last-child) {
      margin-bottom: clamp(20px, 5vmin, 50px);

      @media (max-width: 768px) {
        margin-bottom: clamp(15px, 4vmin, 35px);
      }

      @media (max-width: 480px) {
        margin-bottom: clamp(10px, 3vmin, 25px);
      }
    }
  }
  .content1 {
    text-align: justify;
    grid-area: content1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    line-height: 1.3;

    .contenthh {
      text-align: left;
      grid-area: content;
      display: flex;
      align-items: left;
      justify-content: center;
      flex-direction: column;
      line-height: 1.3;
    }

    @media (max-width: 768px) {
      text-align: left;
      line-height: 1.4;
    }

    @media (max-width: 480px) {
      text-align: left;
      line-height: 1.5;
    }

