      margin-bottom: clamp(20px, 5vmin, 50px);

      @media (max-width: 768px) {
        margin-bottom: clamp(15px, 4vmin, 35px);
      }

      @media (max-width: 480px) {
        margin-bottom: clamp(10px, 3vmin, 25px);
      }
    }
  }

  .button-row {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    gap: 0.7em;

    @media (max-width: 768px) {
      flex-direction: column;
      gap: 0.5em;
      align-items: center;
