      background-color: var(--color-black-alpha);
      border: 2px solid var(--border-color);
      font-size: 30px;
      padding: 0.2em 1.4em;

      @media (max-width: 1024px) {
        font-size: 26px;
        padding: 0.3em 1.2em;
      }

      @media (max-width: 768px) {
        font-size: 22px;
        padding: 0.4em 1em;
      }

      @media (max-width: 480px) {
        font-size: 18px;
        padding: 0.5em 0.8em;
        border-width: 1.5px;
      }
