      background-color: transparent;
      border: none;
      font-size: 18px;
      color: var(--color-grey);

      @media (max-width: 768px) {
        font-size: 16px;
      }

      @media (max-width: 480px) {
        font-size: 14px;
      }
    }

    &.no-style {
    }

    &:hover,
    &:active {
      --border-color: var(--color-Avatar);
    }
  }

  p {
    max-width: 400px;
    margin: 0;

    @media (max-width: 768px) {
      max-width: 90%;
    }

    @media (max-width: 480px) {
      max-width: 95%;
    }
  }
