.loading-bar {
  width: 260px;
  height: 2px;

  background-color: var(--color-grey-dark);

  overflow: hidden;
  position: relative;

  @media (max-width: 768px) {
    width: 200px;
  }

  @media (max-width: 480px) {
    width: 150px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: var(--color-Avatar);
    transform-origin: left center;
    transform: scaleX(calc(1 * var(--loaded)));
  }
}

// États responsive
[data-state="IDLE"],
[data-state="INIT"] {
  #sounds-button {
    display: none;
  }
}

[data-state="INIT"] {
  #sounds-button {
    display: none;
  }
}

[data-state="LOADING"] {
  [data-screen="LOADING"] {
    display: grid;
    grid-template-columns: 0px 1fr;
    grid-template-areas: "space content";

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      grid-template-areas: "content";
    }
  }

  #sounds-button {
    display: none;
  }
}

[data-state="LOAD_ERROR"] {
  #sounds-button {
    display: none;
  }
}

[data-state="TITLE_SCREEN"] {
  [data-screen="TITLE_SCREEN"] {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "space content";

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      grid-template-areas: "content";
    }

    h1 {
      line-height: 1.2em;

      @media (max-width: 480px) {
        line-height: 1.1em;
      }
    }
  }
}

[data-state="CREDITS"] {
  [data-screen="CREDITS"] {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-areas: "content space";

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      grid-template-areas: "content";
    }

    h3,
    .content {
      width: auto;
      text-align: left;

      @media (max-width: 480px) {
        text-align: left;
      }
    }

    ul {
      @media (max-width: 480px) {
        list-style: none;
        padding: 0;
        margin-top: 2rem;
        text-align: left;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin-bottom: 2rem !important;
      }
    }

    li {
      @media (max-width: 480px) {
        margin: 0;
        font-size: 0.9em;
        line-height: 1.4; /* Hauteur de ligne uniforme */
      }
    }

    // Normalise les liens pour avoir la même hauteur
    a {
      @media (max-width: 480px) {
        display: inline; // Reste en ligne
        line-height: 1.4; // Même hauteur que le li
        vertical-align: baseline; // Alignement cohérent
        padding: 0;
        margin: 0;
      }
    }
  }
}
[data-state="INSTRUCTIONS_OumaPortfolioS"]
  [data-screen="INSTRUCTIONS_OumaPortfolioS"]
  .content {
  display: flex !important;
  flex-direction: column !important;
  justify-content: center !important;
  align-items: center; /* centre horizontalement */
  margin: 0 auto; /* pour bien centrer */
  max-width: 90%; /* évite que ça déborde sur petits écrans */
}

