#OumaPortfolio-guide {
  width: 70%;
  max-width: 400px;
  opacity: 0;
  transition: opacity 1s ease-in-out;

  @media (max-width: 768px) {
    width: 80%;
    max-width: 300px;
  }

  @media (max-width: 480px) {
    width: 90%;
    max-width: 250px;
  }

  &.show {
    opacity: 0.5;
  }
}

// Section Instructions - Responsive Grid avec Flèche Guide
[data-state="INSTRUCTIONS_OumaPortfolioS"] {
  [data-screen="INSTRUCTIONS_OumaPortfolioS"] {
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    grid-template-areas: "content space";
    position: relative;
    min-height: 100vh;
    align-items: center;

    .content {
      grid-area: content;
      padding: 2rem;
      display: flex;
      flex-direction: column;
      justify-content: center;

      p {
        font-size: clamp(1rem, 2.5vw, 1.25rem);
        line-height: 1.6;
        margin-bottom: 2rem;
      }

      button {
        padding: 0.75rem 2rem;
        font-size: clamp(0.9rem, 2vw, 1.1rem);
        border-radius: 8px;
        cursor: pointer;
        transition: all 0.3s ease;
        max-width: fit-content;
      }
    }

    // Flèche guide spécifique à cette section
    .pause-guide-arrow {
      position: absolute;
      top: 20px;
      right: 80px;
      z-index: 1000;
      pointer-events: none;
      opacity: 0.8;
      animation: pulseArrow 2s infinite ease-in-out;

      // SVG flèche réaliste pointant vers le bas-gauche
      svg {
        width: 32px;
        height: 32px;
        fill: currentColor;
        filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.3));
        transform: rotate(-135deg); // Pointe vers le bouton pause
      }
    }

    // Responsive breakpoints - Tablettes
    @media (max-width: 1024px) {
      grid-template-columns: 1fr 1fr;

      .content {
        // Garde la même disposition, pas de centrage
        padding: 1.5rem;
      }

      .pause-guide-arrow {
        top: 15px;
        right: 60px;

        svg {
          width: 28px;
          height: 28px;
        }
      }
    }

    // Tablettes en portrait et petits laptops
    @media (max-width: 820) {
      grid-template-columns: 1fr 1.2fr;
      // Garde encore la grill
      position: absolute !important;
      top: -20% !important;

