.OumaPortfolios {
  inset: unset;
  bottom: 3vmin;
  right: 3vmin;
  z-index: 10;
  max-width: 50%;
  display: none;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 1.5rem;

  // Copie de min-width mais avec p caché et svg plus petit
  @media (max-width: 1025px) {
    max-width: 40%;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    gap: 0;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    .OumaPortfolio-path {
      width: 70px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      .check {
        display: none;
      }
      svg {
        width: 50%;
        fill: none;
      }
    }
    .info {
      display: none;
      flex-direction: column;
      width: 100% !important;

      h4 {
        margin-bottom: 0.2rem;
      }
      p {
        display: none !important;
      }
    }
    .charge-path {
      stroke-width: 6;
      stroke-dasharray: var(--length) var(--length);
      stroke-dashoffset: calc(((1 - var(--charge))) * var(--length));
    }
    .guide-path {
      stroke: rgba(255, 255, 255, 0.2);
    }
    .OumaPortfolio-details {
      display: flex;
      flex-direction: row;
      gap: 0.8rem;
      align-items: center;
      z-index: 2;
    }
    .background {
      position: absolute;
      inset: 0;
      border: solid 2px var(--color-grey);
      background: 
  // Grille orange subtile
        repeating-linear-gradient(
          0deg,
          transparent,
          transparent 50px,
          rgba(51, 17, 0, 0.1) 50px,
          rgba(51, 17, 0, 0.1) 51px
        ),
        repeating-linear-gradient(
          90deg,
          transparent,
          transparent 50px,
          rgba(51, 17, 0, 0.1) 50px,
          rgba(51, 17, 0, 0.1) 51px
        ),
        // Dégradé principal
        radial-gradient(
            ellipse 150% 80% at 50% 100%,
            #4d1f00 0%,
            /* Orange au fond */ #261000 40%,
            /* Brun foncé */ #000000 80% /* Noir */
          );
    }
    a {
      font-size: 16px;
    }
    &.corner {
      cursor: pointer;
      pointer-events: all;
      display: grid;
      .OumaPortfolio-path {
        &.ready {
          .check {
            opacity: 1;
            transform: translate(-50%, -200%);
          }
        }
      }
      &:hover {
        .background {
          border-color: var(--color-Avatar);
        }
      }
    }
    &.full {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      bottom: 50%;
      gap: 2rem;
      transform: translateY(50%);
      padding: 2rem 3rem;
      .OumaPortfolio-path {
        width: 160px;
        .check {
          transition: none;
        }
        svg {
          --charge: 1 !important;
        }
      }
      .info {
        display: flex;
      }
    }
  }

  // Styles uniquement pour les grands écrans (desktop)
  @media (min-width: 1025px) {
    .OumaPortfolio-path {
      width: 70px;
      position: relative;
      .check {
        display: none;
      }
      svg {
        width: 60%;
        fill: none;
      }
