      }
    }
    .charge-path {
      stroke-width: 6;
      stroke-dasharray: var(--length) var(--length);
      stroke-dashoffset: calc(((1 - var(--charge))) * var(--length));
    }
    .guide-path {
      stroke: rgba(255, 255, 255, 0.2);
    }
    .OumaPortfolio-details {
      display: flex;
      flex-direction: row;
      gap: 0.8rem;
      align-items: center;
      z-index: 2;
    }
    .background {
      position: absolute;
      inset: 0;
      border: solid 2px var(--color-grey);
      background-color: var(--color-black-alpha);
    }
    a {
      font-size: 16px;
    }
    &.corner {
      cursor: pointer;
      pointer-events: all;
      display: grid;
      .OumaPortfolio-path {
        &.ready {
          .check {
            opacity: 1;
            transform: translate(-50%, -200%);
          }
        }
      }
      &:hover {
        .background {
          border-color: var(--color-Avatar);
        }
      }
    }
    &.full {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      bottom: 50%;
      gap: 2rem;
      transform: translateY(50%);
      padding: 2rem 3rem;
      .OumaPortfolio-path {
        width: 160px;
        .check {
          transition: none;
        }
        svg {
          --charge: 1 !important;
        }
      }
      .info {
        display: flex;
      }
    }
  }
